.App {
  font-family: sans-serif;
  text-align: center;
}
.card-grid {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
}
.card {
  align-content: center;
  border: 1px solid grey;
  border-radius: 10px;
  overflow: hidden;
  margin-bottom: 12px;
  margin-right: 12px;
}
.card p {
  padding: 5px;
  text-align: center;
}

.card img {
  max-height: 170px;
}
